<template>
    <div class="packing_box">
      <div class="packing_cont">
        <span></span>
      </div>
      <div class="packing">
        <div class="packing_boxs" style="width: 10%"></div>
        <div class="packing_boxs" style="width: 40%;padding-left: 1px"><input type="text" v-model="infos.packing_name"></div>
        <div class="packing_boxs" style="width: 10%"></div>
        <div class="packing_boxs" style="width: 40%">
          <Select v-model="model1" placeholder="" style="width:200px">
            <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
          </Select>
        </div>
      </div>
      <div class="packing">
        <div class="packing_boxs" style="width: 10%"></div>
        <div class="packing_boxs" style="width: 40%;padding-left: 1px">
          <Select v-model="model2" placeholder="" style="width:200px">
          <Option v-for="item in cityList2" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
        </div>
        <div class="packing_boxs" style="width: 10%"></div>
        <div class="packing_boxs" style="width: 40%"><input type="text" v-model="infos.material"></div>
      </div>
      <div class="packing">
        <div class="packing_boxs" style="width: 10%"></div>
        <div class="packing_boxs" style="width: 40%;padding-left: 1px">
          <Select v-model="model3" placeholder="" style="width:200px">
            <Option v-for="item in cityList3" :value="item.value" :key="item.value">{{ item.label }}</Option>
          </Select>
        </div>
        <div class="packing_boxs" style="width: 50%"></div>
      </div>
      <div style="text-align: center;margin-top: 30px">
        <button class="submit_btn"></button>
      </div>
    </div>
</template>

<script>
    export default {
        name: "packing",
      data () {
        return {
          infos:{
            packing_name:"",
            material:"",
          },
          cityList: [
            {
              value: '',
              label: ''
            },
            {
              value: '',
              label: ''
            },
            {
              value: '',
              label: ''
            }
          ],
          model1: '',
          cityList2: [
            {
              value: '',
              label: ''
            },
            {
              value: '',
              label: ''
            },
            {
              value: '',
              label: ''
            },
            {
              value: '',
              label: ''
            },
            {
              value: '',
              label: ''
            }
          ],
          model2: '',
          cityList3: [
            {
              value: '',
              label: ''
            },
            {
              value: '',
              label: ''
            },
            {
              value: '',
              label: ''
            }
          ],
          model3: ''
        }
      }
    }
</script>

<style scoped>
  .packing_cont {
    height: 60px;
    width: 100%;
    line-height: 60px;
    color: white;
    font-size: 18px;
    text-indent: 30px;
    background: linear-gradient(to right, rgb(112, 175, 226), rgb(59, 120, 165));
  }
  .packing {
    display: flex;
  }
  .packing_boxs {
    height: 50px;
    border: 1px solid #aaa;
    line-height: 50px;
    text-indent: 15px;
    background: rgb(238, 238, 238);
  }

  .packing_boxs input {
    width: 300px;
    height: 35px;
  }

  .submit_btn {
    width: 80px;
    height: 30px;
    border: none;
    background: #2db7f5;
    border-radius: 5px;
    color: white;
  }
</style>



